.cartContainer .offerProduct .productTitle{
font-weight: 600;
}

.cartContainer .offerProduct .originalPrice, .cartContainer .summaryCard .originalPrice{
    font-size:18px;
    color:rgba(0,0,0,0.4);
    text-decoration:line-through;
}

.cartContainer .offerProduct .offerPrice, .cartContainer .summaryCard .offerPrice{
    font-size:20px;
}

.cartContainer .offerProduct .addToBag{
    background:#000;
    color:#fff;
    font-size:12px;
}

.cartContainer .availableOffersTitle{
    font-weight:600;
    font-family: 'TabarroPro' !important;
}

.cartContainer .applyCouponTitle{
    font-weight:600;
    font-family: 'TabarroPro' !important;
}

.cartContainer .applyBtn{
    background:#000;
    color:#fff;
}

.cartContainer .viewOffers{
    font-weight: 600;
}

.cartContainer .priceDetailsTitle{
    font-weight:600;
    font-family: 'TabarroPro' !important;
}

.cartContainer .priceDetails{
    font-weight:600;
    background:#fff;
}

.cartContainer .letsChat{
    font-weight:600;
    font-size:26px;
    font-family: 'TabarroPro' !important;
}

.cartContainer .contactDetails{
    font-size:20px;
}

.cartContainer .editItems{
	display: flex;
	border: 1px solid #404040;
	border-radius: 4px;
	width: 80px;
	justify-content: space-between;
	align-items: center;
	align-content: center;
}

.cartContainer .editItems #plus-btn, .cartContainer .editItems #minus-btn{
    background:#fff;
    border:none;
}

.cartContainer .editItems .number{
    width:7vw;
    text-align: center;
}

.cartSummary{
    font-family: 'TabarroPro' !important;
}

.offerProduct .productTitle, .priceProduct{
    padding-left:12px;
}

.cartContainer .viewMore {
    color: red !important;
    font-weight: normal !important;
    font-size: 12px;
    position: absolute;
    right: 5%;
    top:75%;
}

.cartContainer .viewLess {
    color: red !important;
    font-weight: normal !important;
    font-size: 12px;
    position: absolute;
    right: 5%;
    bottom:3%;
}

.cartContainer .availOffers{
    padding:10px;
    background:#fff;
    border-radius:10px;
    margin:10px 0px;
    position:relative;
    height:105px;
    overflow:hidden;
    padding-bottom:25px;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

.cartContainer .availOffers>div{
    height:9.983vh;
    overflow: hidden;
}

.cartContainer .availOffers li:nth-child(n+3){
    display: none;
}

.cartContainer .availOffers.expandOffersCard li{
    display: list-item;
}


.cartContainer .availOffers.expandOffersCard,
 .cartContainer .availOffers .expandOffers{
    height:100%;
}

.cartContainer .productsDropdown{
    max-width:95px;
}

.emptyContainer img{
    width:50%;
}

.similar-products {
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and (max-width:520px){

    .cartContainer{
        padding-top:20px;
        padding-bottom:20px;
    }

    .cartContainer .contactDetails {
        font-size: 16px;
    }
    .priceDetails {
        width: 100% !important;
    }

    .cartContainer .viewOffers{
        font-size:14px;
    }

    .cartContainer .offerProduct .offerPrice, .cartContainer .summaryCard .offerPrice{
        font-size:16px;
    }

    .cartContainer .offerProduct .originalPrice, .cartContainer .summaryCard .originalPrice {
        font-size:12px;
    }
    .cartContainer .editItems {
        /* width:20vw; */
        width: auto;
        overflow: hidden;
        color:#000;
        padding-top: 2px;
    padding-bottom: 2px;

    }

    .cartContainer .productsDropdown.form-select:focus{
        border: 1px solid #ced4da;
        box-shadow: none;
    }

    .emptyContainer img{
        height:95%;
        width: 100%;
    }
    
}