.available-coupons-wrapper {
  width: 275px;
}
.available-coupons-wrapper .coupon-name {
  border-style: solid;
  padding: 0.3rem;
  text-align: center;
  background-color: black;
  color: white;
  border-radius: 5px;
  border-color: black;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: large;
}

.available-coupons-wrapper .coupon-details {
  border-style: dashed;
  border-radius: 5px;
  border-top: 0px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
}

.available-coupons-wrapper .coupon-code {
  display: flex;
  justify-content: space-between;
  padding: 0.8rem;
  align-items: center;
}

.available-coupons-wrapper .coupon-code-text {
  border-style: dotted;
  padding: 0.3rem;
  border-width: 2px;
}

.available-coupons-wrapper .apply {
  cursor: pointer;
}

.available-coupons-wrapper .coupon-description {
  padding: 0.8rem;
}
.available-coupons-wrapper img {
  height: 15px;
}
.available-coupons-wrapper .applied {
  color: green;
}
.available-coupons-wrapper .disable {
  cursor: not-allowed;
}
@media screen and (max-width: 575.98px) {
  .available-coupons-wrapper {
    width: auto;
  }
}
